import {
	formatDate
} from "../../common/js/date.js"
import wPicker from "@/components/w-picker/w-picker.vue";
import mButton from '../../components/m-button.vue'
export default {
	data() {
		return {
			ordernumber: '',
			date: "请选择时间",
			values: '',
			btnType: 'primary',
			btnValue: "查询",
			startDate: '2010-00-00',
			endDate: '2030-12-31',
			name: '', //区分页面
			page: 1,
			pageSize: 10,
			resultInfo: {
				result: "2019-12-20 10:00:00"
			},
			 from:'',//第一个时间
			 to:'',//第二个时间
		};
	},
	onLoad(option) {
		this.name = option.name
	},
	methods: {
		bindChange() {
			this.$refs.range.show();
		},
		onConfirm(val) {
			console.log(val);
			this.date = val.result;
			this.from = val.from;
			this.to = val.to;
			
		},
		search() {
			if (this.date == '请选择时间') this.date = '';
			if (this.name == "orderList") {
				this.test.post('order/search_order', {
					no: this.ordernumber, //订单编号
					start_time: this.from,
					end_time: this.to,
					page: this.page,
					pageSize: this.pageSize,
				}).then(res => {
					console.log(res)
					if (res.statusCode == 200 && res.data.errorCode == 0) {
						if (res.data.value.length <= 0) {
							uni.showToast({
								title: '没有找到相关的订单，请重新查询',
								icon: 'none',
							})
						} else if (res.data.value.length > 0) {
							uni.redirectTo({
								url: '../orderList/orderList?from=' + this.from +'&to=' + this.to + '&ordernumber=' + this.ordernumber + '&times=' + this.date
							})

						}
					}
				}).catch(err => {
					console.log(err)
				})
			} else if (this.name == "reserveOilList") {
				this.test.post('order/search_reserve', {
					no: this.ordernumber, //订单编号
					start_time: this.from,
					end_time: this.to,
					page: this.page,
					pageSize: this.pageSize,
				}).then(res => {
					console.log(res);
					if (res.statusCode == 200 && res.data.errorCode == 0) {
						if (res.data.value.length <= 0) {
							uni.showToast({
								title: '没有找到相关的订单，请重新查询',
								icon: 'none'
							})
						} else if (res.data.value.length > 0) {
							uni.redirectTo({
								url: '../reserveOilList/reserveOilList?times=' + this.date + '&ordernumber=' + this.ordernumber + '&from=' + this.from +'&to=' + this.to
							})

						}
					}
				}).catch(err => {
					console.log(err)
				})
			}

		}
	},
	components: {
		wPicker,
		mButton
	},
}
